<template>
  <div class="search-container">
    <van-search
      v-model="keywords"
      placeholder="请输入搜索关键词"
      show-action
      @search="onSearch"
      @cancel="onCancel"
    />
    <van-cell-group v-if="thinkList.length !== 0">
      <van-cell icon="search" v-for="(item, index) in thinkList" :key="index">
        <template slot="title">
          <div v-html="item.colorItem" @click="goList(item.item)"></div>
        </template>
      </van-cell>
    </van-cell-group>
    <van-cell-group v-else>
      <van-cell title="搜索历史">
        <template slot="right-icon">
          <van-icon name="delete" v-show="isDelHistory" @click="delHistory=false" />
          <div v-show="!isDelHistory">
            <span @click="delSearchAll">全部删除&nbsp;</span>
            <span @click="delHistory=true">&nbsp;&nbsp;完成</span>
          </div>
        </template>
      </van-cell>
      <van-cell icon="search" :title="item" v-for="(item, index) in searchHistoryList" :key="index" @click="goList(item)">
        <template slot="right-icon">
          <van-icon name="close" v-if="!isDelHistory" @click.stop="delSearch(index)" />
        </template>
      </van-cell>
    </van-cell-group>
  </div>
</template>

<script>
import { thinkArticle } from "@/api/article";
export default {
  data() {
    return {
      keywords: "",
      isDelHistory: true,
      thinkList: [],
      searchHistoryList: JSON.parse(window.localStorage.getItem("search")) || []
    };
  },
  methods: {
    onSearch() {
      this.goList(this.keywords);
    },
    onCancel() {},
    // 跳转到 list 路由
    goList(item) {
      // 将搜索关键字添加到历史搜索记录数组中
      if (this.keywords) {
        this.searchHistoryList.unshift(this.keywords);
      }
      // 对历史搜索记录数组去重
      this.searchHistoryList = [...new Set(this.searchHistoryList)];
      // 把历史搜索记录添加到 localStorage 中
      window.localStorage.setItem(
        "search",
        JSON.stringify(this.searchHistoryList)
      );
      this.$router.push(`/list/${item}`);
    },
    // 删除一条历史搜索记录
    delSearch(index) {
      this.searchHistoryList.splice(index, 1);
      window.localStorage.setItem(
        "search",
        JSON.stringify(this.searchHistoryList)
      );
    },
    // 删除全部历史搜索记录
    delSearchAll() {
      this.searchHistoryList = [];
      window.localStorage.removeItem('search');
    }
  },
  watch: {
    // 根据搜索关键字获取搜索结果
    async keywords(newVal, oldVal) {
      if (!newVal.trim().length) {
        this.thinkList = [];
        this.isKeywords = true;
        return;
      }
      let res = await thinkArticle(newVal);
      this.thinkList = res.options;
      this.isKeywords = false;
      // 给搜索关键字加高亮
      this.thinkList = this.thinkList.map(item => {
        return {
          colorItem: item
            .split(newVal)
            .join(`<span style="color: red">${newVal}</span>`),
          item
        };
      });
    }
  }
};
</script>

